.design_item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-bottom: 170px;

  &-practical { margin-bottom: 220px; }
  &-slip-resistant { margin-bottom: 220px; }
  &-comfort { margin-bottom: 200px; }
  &-support { margin-bottom: 220px; }

  &-style, &-practical, &-comfort { max-width: 380px; }
  &-support { width: 360px; }
  &-slip-resistant { width: 540px; }
  &-refinement { width: 490px; }

  &__title {
    font-family: $sans;
    font-size: 5.625rem;
    margin: 0 0 -10px 0;
    padding: 0;
    line-height: 1;
    letter-spacing: 4px;
    .letter {
      display: inline-block;
    }
  }

  &__text {
    margin: 0;
    padding: 0;
    font-size: 14px;
    line-height: 1.8;
    color: #7b7b7b;
  }

  &-style { left: 14%; }
  &-practical { left: 42%; }
  &-slip-resistant { left: 45%; }
  &-comfort { left: 34%; }
  &-support { left: 29%; }
  &-refinement { left: 38%; }

  &__img {
    position: absolute;
    width: 360px;
    img {
      width: 100%;
    }
  }

  &-style {
    .design_item__img {
      bottom: -32%;
      right: -158%;
    }
  }

  &-slip-resistant {
    .design_item__img {
      bottom: -125%;
      left: -98%;
    }
  }

  &-support {
    .design_item__img {
      bottom: -20%;
      right: -150%;
    }
  }

  &-refinement {
    .design_item__img {
      bottom: -40%;
      left: -83%;

      width: 250px;
    }
  }
}
