@media (max-width: 1080px) {
  .design_item {
    &__img {
      width: 260px;
    }

    &-style { left: 4%; }
    &-practical { left: 32%; }
    &-slip-resistant { left: 35%; }
    &-comfort { left: 24%; }
    &-support { left: 19%; }
    &-refinement { left: 28%; }

    &-style {
      .design_item__img {
        right: -100%;
      }
    }

    &-slip-resistant {
      .design_item__img {
        left: -50%;
      }
    }

    &-support {
      .design_item__img {
        right: -100%;
      }
    }

    &-refinement {
      .design_item__img {
        left: -60%;
        width: 250px;
      }
    }
  }
}

@media (max-width: 880px) {
  .design_item {

    &-style { left: 2%; }
    &-practical { left: 22%; }
    &-slip-resistant { left: 20%; }
    &-comfort { left: 20%; }
    &-support { left: 9%; }
    &-refinement { left: 25%; }

    &-style {
      .design_item__img {
        right: -100%;
      }
    }

    &-slip-resistant {
      .design_item__img {
        left: -50%;
      }
    }

    &-support {
      .design_item__img {
        right: -100%;
      }
    }

    &-refinement {
      .design_item__img {
        left: -60%;
        width: 250px;
      }
    }
  }
}

@media (max-width: 700px) {
  .design__list {
    align-items: center;
  }
  .design_item {
    &__img {
      display: none;
    }

    &-style { left: 0; }
    &-practical { left: 0; }
    &-slip-resistant { left: 0; }
    &-comfort { left: 0; }
    &-support { left: 0; }
    &-refinement { left: 0; }
  }
}

@media (max-width: 580px) {
  .design_item {
    width: 100%;
    padding: 0 15px;
    margin-bottom: 100px;
    &__title {
      margin-bottom: 20px;
    }
  }
}
