.shop {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;

  &__main {
    z-index: 99999;
    position: absolute;
    left: 50%;
    top: 53.8%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    font-family: $sans;
    font-size: 11.25rem;
  }

  &__subtitle {
    text-align: center;
    max-width: 430px;
    padding: 0 15px;
    font-size: 16px;
    line-height: 1.8;
    margin-top: -15%;
    margin-bottom: 20px;
  }

  &__bg {
    position: absolute;
    bottom: 0;
    left: calc(50% - 40vw);
    width: 80vw;
    height: 60vh;
    background-image: linear-gradient(45deg, rgb(123, 231, 215) 1%, rgb(123, 231, 215) 40%, rgb(123, 231, 215) 40%, rgb(134, 164, 227) 100%);
    //transition: transform 0.5s;
  }


  &__button {
    border-color: black;
    color: black;

    &:hover {
      background-color: black;
      color: white;
    }
  }

  &__social {
    z-index: 9999;
    width: 150px;
    position: absolute;
    bottom: 20px;
    left: calc(50% - 75px);
    &_list {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;

    }
  }
}
