.button {
  font-family: $sans;
  font-size: 18px;
  text-decoration: none;
  display: inline-block;
  padding: 0 60px;
  border: 1px solid;
  border-radius: 50px;
  height: 42px;
  line-height: 50px;
  transition: 0.5s color, 0.5s background;
}

@media (max-width: 600px) {
  .button {
    line-height: 42px;
  }
}
