.card {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin: 150px 0;

  &__describe, &__images {
    width: 50%;
    position: relative;
    height: 100vh;
  }

  &__describe_container {
    width: 100%;
    padding: 0 24.5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__head, &__name, &__size_title, &__size_item, &__button {
    font-family: $sans;
  }

  &__head {
    font-size: 26px;
    margin-bottom: 15px;
  }

  &__name {
    font-size: 70px;
    margin-bottom: 25px;
  }

  &__description {
    font-size: 14px;
    width: 100%;
    margin-bottom: 25px;
    line-height: 1.8;
  }
  &__size {
    width: 258px;
  }
  &__size_title {
    font-size: 18px;
    margin-bottom: 24px;
  }

  &__size_list {
    width: 100%;
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__size_item {
    font-size: 14px;
  }

  &__button {
    span {
      display: inline-block;
      letter-spacing: 1px;
    }


  }

  &__button:hover span {
    animation-name: animation;
    animation-duration: 0.5s;
    //transform: translateY(-5px);
    &:nth-of-type(1) {
      animation-delay: 0ms;
    }
    &:nth-of-type(2) {
      animation-delay: 100ms;
    }
    &:nth-of-type(3) {
      animation-delay: 200ms;
    }
    &:nth-of-type(4) {
      animation-delay: 300ms;
    }
  }

  &__images_gradient {
    position: absolute;
    top: 10%;
    right: 0;
    width: 96%;
    height: 75vh;
  }

  &__images_foot {
    position: absolute;
    right: 0;
    bottom: 0;
  }

  &__letter_wrap {
    position: absolute;
    top: 50%;
    left: 48%;
    transform: translate(-50%, -50%);
  }

  &-revers {
    flex-direction: row-reverse;

    .card__images_gradient {
      left: 0;
    }

    .card__images_foot {
      left: 0;
    }
  }
}

@import "./card_color";
@import "./media";

@keyframes animation {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-8px);
  }

  100% {
    transform: translateY(0);
  }
}
