@import './design_item';

.design {
  max-width: 1240px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    font-family: $sans;
    font-size: 10rem;
    margin-bottom: 200px;
  }

  &__list {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__item {
    position: relative;
  }
}

@import './media';
